import Cinput from '../components/Input.vue'

export default {
    name: 'mongooseOperations',
    data () {
        return {
            items: [], // 用户信息
            btnFont: '新增用户信息',
            type: 'add',  //add:新增, edit:修改
            id: '',
            name: '',
            phone: '',
            email: '',
            github: '',
            empty: false
        }
    },
    mounted(){
        this.getUserInfo();
    },
    components: {
        Cinput
    },
    methods: {
        getUserInfo: function(e){ // 查询已有的用户信息
            this.$axios.get('/api/user/getUserInfo')
            .then((res) => {
                if(res.status == 200) { //发送成功
                    if(res.data.errors) { //参数错误
                        this.$vux.toast.show({text: res.data._message, type: 'cancel'})
                    }
                    else { //请求正确
                        if(res.data.length == 0) this.empty = true
                        else this.empty = false
                        this.items = res.data
                    }
                }
            })
            .catch((reject) => {
                console.log(reject)
                this.$vux.toast.show({text: '获取用户信息请求失败！',type: 'cancel'})
            })
        },
        editUserInfo: function(id){ // 修改已有用户信息
            for(let i = 0; i < this.items.length; i++) {
                // debugger
                if(this.items[i]._id == id) { 
                    this.name = this.items[i].name,
                    this.phone = this.items[i].phone,
                    this.email = this.items[i].email,
                    this.github = this.items[i].github
                }
            }
            this.btnFont = '修改用户信息', this.type = 'edit', this.id = id
        },
        deleteUserInfo: function(id){ // 删除已有用户信息
            let params = {_id: id}
            this.$axios.post('/api/user/removeUserInfo', params)
            .then((res) => {
                if(res.status == 200) { //发送成功
                    if(res.data.errors) { //参数错误
                        this.$vux.toast.show({text: res.data._message, type: 'cancel'})
                    }
                    else { //请求正确
                        this.getUserInfo() //刷新列表
                        this.$vux.toast.show({text: '删除成功！'})
                    }
                }
            })
            .catch((reject) => {
                console.log(reject)
                this.$vux.toast.show({text: '删除用户请求失败！', type: 'cancel'})
            })
        },
        submitUserInfo: function(e){ // 创建或修改一个账号密码
            if(this.type == 'add') this.url = '/api/user/createUserInfo'
            else this.url = '/api/user/updateUserInfo'

            let params = {name: this.name, phone: this.phone, email: this.email, github: this.github}
            this.id ? params._id = this.id : ''

            // 创建或修改一个账号密码
            this.$axios.post(this.url, params)
            .then((res) => {
                if(res.status == 200) { //发送成功
                    if(res.data.errors) { //参数错误
                        this.$vux.toast.show({text: res.data._message, type: 'cancel'})
                    }
                    else { //请求正确
                        this.message = (this.type == 'add') ? '新增成功!' : '修改成功!'
                        this.getUserInfo() //刷新列表
                        this.$vux.toast.show({text: this.message})
                    }
                }
            })
            .catch((reject) => {
                console.log(reject)
                this.$vux.toast.show({text: '提交用户信息请求失败！', type: 'cancel'})
            })
        }
    }
}   



